/* inject:components:less */
nav#et-navbar {
  position: relative;
  font-size: 14px;
  height: 5em;
  background-color: #fefefe;
}
nav#et-navbar > .container {
  position: relative;
  max-width: 1024px;
  margin: 0 auto;
  height: inherit;
  display: flex;
}
nav#et-navbar .brand {
  width: 4.5em;
  display: flex;
  align-items: center;
  flex: 0 0 auto;
}
nav#et-navbar .brand > img {
  width: 100%;
  box-sizing: border-box;
  padding: 0.2em 1em;
}
nav#et-navbar .navbar-nav {
  position: relative;
  cursor: pointer;
  padding: 0 1em;
  user-select: none;
  color: #a03033;
  display: flex;
  align-items: center;
  overflow: hidden;
  background-color: transparent;
  transition: background-color 0.4s ease-in-out;
}
nav#et-navbar .navbar-nav > * {
  padding: 0.1em;
}
nav#et-navbar .navbar-nav:hover {
  background-color: rgba(160, 48, 51, 0.08);
}
@media (max-width: 650px) {
  nav#et-navbar > .container {
    max-width: 500px;
  }
  nav#et-navbar > .container > .brand {
    width: 4em;
  }
  nav#et-navbar > .container > .navbar-nav {
    flex-grow: 1;
  }
  nav#et-navbar > .container > .navbar-nav > i {
    margin: 0 auto;
  }
  nav#et-navbar > .container > .flex,
  nav#et-navbar > .container .navbar-nav > span {
    display: none;
  }
}
/* endinject */
/* inject:partials:less */
.circular {
  width: 300px;
  height: 300px;
  border-radius: 150px;
}
.vcards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.vcards .vcard {
  display: flex;
  align-items: center;
  background-image: url("../../../assets/images/ETlogos/logo.svg");
  background-position: left;
  background-repeat: no-repeat;
  background-size: 7em;
  width: 300px;
  margin: 10px;
  padding: 2px;
}
.vcards .vcard .vcard-image {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  background-size: cover;
  margin: 0px 10px;
  border: 1px solid #a03033;
}
.vcards .vcard span {
  font-weight: bold;
}
.paginator {
  display: flex;
  justify-content: center;
}
.login-container {
  margin: 0 auto;
  padding: 50px 0;
  max-width: 300px;
  text-align: center;
}
.login-container .login-btn-group-vertical > .btn {
  margin: 2px 0;
}
.login-container .login-header {
  margin: 25px 0 5px 0;
}
.login-container .login-content {
  padding: 15px 5px;
}
.login-container .login-separator {
  margin: 15px 0;
}
.login-container .login-separator:before,
.login-container .login-separator:after {
  background-color: #aaa;
  content: "";
  display: inline-block;
  height: 1px;
  position: relative;
  vertical-align: middle;
  width: 40%;
}
.login-container .login-separator > .separator-text {
  padding: 0 15px;
}
.video-wrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  height: 0;
}
.video-wrapper > iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.commersial {
  overflow: hidden;
