}
.programme-header > .header-content .md-title {
  font-size: 2.2em;
  margin-left: 2.5em;
}
.skew-lb,
.skew-rb {
  position: relative;
}
.skew-lb > *,
.skew-rb > * {
  z-index: 1;
}
.skew-lb:before,
.skew-rb:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: inherit;
  top: 50%;
}
.skew-lb:before {
  transform: skewY(-3deg);
}
.skew-rb:before {
  transform: skewY(3deg);
}
/* endinject */
